/* MY CSS STYLES */
/* grid: https: //studiomeal.com/archives/533 */
/* google fonts: https: //fonts.google.com/ */
.container-box {
  /* display: */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.content-box {
  background-color: #A1B297;
  border-radius: 1.5vw;
  /* dimension: */
  height: 18rem;
  width: 60rem;
  max-height: 70vh;
  max-width: 90vw;
  margin: 2vh;
  padding: 3vh;
  /* grid: */
  display: grid;
  grid-template-columns: 16fr 5fr 4fr;
  grid-template-rows: 3fr 3fr 2fr;
  /* grid-template-areas:
    "text text text text text text . wpm"
    "text text text text text text . acc"
    "input input input input input redo . color-buttons"; */
}

.word-list-box {
  background-color: #EDEDED;
  border-radius: 1vw;
  overflow-y: scroll;
  /* dimension: */
  margin: 1vh;
  /* TODO: fix padding for all with this setting */
  padding: 1vh 2vh 1vh 2vh;
  /* font: */
  font-family: 'Baloo 2', serif;
  font-weight: 400;
  font-size: 1.35rem;
  /* grid: */
  grid-column: 1 / 3;
  grid-row: 1 / 3;
}

.word-list-box::-webkit-scrollbar {
  display: none;
}

.wpm-acc-box {
  background-color: #EDEDED;
  border-radius: 1vw;
  /* dimension: */
  margin: 1vh 1vh 1vh 2vh;
  padding: 1vh 2vh 1vh 2vh;
  /* font: */
  font-family: 'Bakbak One', serif;
  font-weight: 400;
  color: #5B5E5A;
}

.wpm-box {
  /* grid: */
  grid-column: 3 / 4;
  grid-row: 1 / 2;
}

.acc-box {
  /* grid: */
  grid-column: 3 / 4;
  grid-row: 2 / 3;
}

.wpm-acc-label {
  font-size: min(1rem, 3vh);
  text-align: start;
  /* display: */
  display: block;
}

.wpm-acc-value {
  font-size: min(2.25rem, 8vh);
  text-align: end;
  /* display: */
  display: block;
}

.content-box input {
  border-radius: 1vw;
  border-width: 0.001rem;
  border-color: transparent;
  /* dimension:  */
  margin: 1vh;
  padding: 0vh 2vh 0vh 2vh;
  /* font */
  font-family: 'Baloo 2', serif;
  font-size: min(1.25rem, 8vh);
  font-weight: 300;
  /* grid: */
  grid-column: 1 / 2;
  grid-row: 3 / 4;
}

.content-box input:focus {
  outline-color: rgb(95, 172, 255)5, 235, 235;
}

.redo-button {
  background-color: #F8ECCD;
  border-radius: 1vw;
  border: none;
  /* dimension:  */
  margin: 1vh;
  /* font: */
  font-family: 'Bakbak One', serif;
  font-size: min(2rem, 8vh);
  font-weight: 400;
  color: #788173;
  /* grid: */
  grid-column: 2 / 3;
  grid-row: 3 / 4;
}

.num-button-container {
  /* dimension:  */
  margin: 1vh 1vh 1vh 2vh;
  /* grid: */
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 3;
  grid-row-end: 4;
  /* grid for children: */
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
}

.num-button {
  background-color: rgb(255, 114, 114);
  border-radius: 50%;
  border-style: none;
  opacity: 70%;
  /* dimension: */
  aspect-ratio: 1 / 1;
  width: 80%;
  padding: 0;
  /* font: */
  font-family: 'Bakbak One', serif;
  font-size: min(1rem, 3vh);
  font-weight: 400;
  color: rgb(240, 240, 240);
}

/* numWords = 10 */
.num-button:nth-of-type(1) {
  background-color: #DC8282;
  grid-column: 1 / 2;
  justify-self: start;
  align-self: start;
}

/* numWords = 25 */
.num-button:nth-of-type(2) {
  background-color: #ADD2EC;
  grid-column: 2 / 3;
  width: 80%;
  justify-self: center;
  align-self: end;
}

/* numWords = 50 */
.num-button:nth-of-type(3) {
  background-color: #EFCC70;
  grid-column: 3 / 4;
  justify-self: end;
  align-self: start;
}

.redo-button:hover,
.num-button:hover {
  cursor: pointer;
}

.selected-num {
  /* text-decoration: underline; */
  color: white;
  opacity: 100%;
  filter: brightness(1.025);
  box-shadow: inset 0rem 0rem 0.1rem #7f8d77;
}